:root {
  --bg: #faf9f6;
  --fg: #0d0d0d;
  --accent: #f97316;
  --accent-hover: #ea6c0a;
  --muted: #6b7280;
  --card: #ffffff;
  --border: #e5e7eb;
  --dark-bg: #0a0a0f;
  --dark-card: #111118;
}
* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--bg);
  color: var(--fg);
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.font-serif {
  font-family: "Playfair Display", serif;
}
.mesh-bg {
  position: fixed;
  inset: 0;
  z-index: 0;
  overflow: hidden;
  pointer-events: none;
}
.mesh-bg svg {
  width: 100%;
  height: 100%;
  opacity: 0.12;
}
.glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.gradient-text {
  background: linear-gradient(135deg, #f97316, #fb923c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.btn-accent {
  background: #0d0d0d;
  color: #faf9f6;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 0.813rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  padding: 12px 28px;
  border-radius: 2px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
}
.btn-accent:hover {
  background: #f97316;
  transform: translateY(-1px);
  box-shadow: 0 4px 20px rgba(249, 115, 22, 0.35);
}
.btn-accent:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.btn-outline {
  background: transparent;
  color: #0d0d0d;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 0.813rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  padding: 11px 27px;
  border-radius: 2px;
  border: 1.5px solid #0d0d0d;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.btn-outline:hover {
  background: #0d0d0d;
  color: #faf9f6;
}
.badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-family: "Inter", sans-serif;
  font-size: 0.688rem;
  font-weight: 600;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  padding: 5px 12px;
  border: 1.5px solid #f97316;
  border-radius: 2px;
  color: #f97316;
  background: rgba(249, 115, 22, 0.05);
}
.card-hover {
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}
.card-hover:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}
.code-area {
  font-family: "Fira Code", "Fira Mono", monospace;
  font-size: 0.875rem;
  line-height: 1.6;
  background: #1e1e2e;
  color: #cdd6f4;
  border-radius: 8px;
  padding: 20px;
  resize: vertical;
  outline: none;
  border: 1.5px solid #2a2a3c;
  transition: border-color 0.2s;
  width: 100%;
}
.code-area:focus {
  border-color: #f97316;
}
